const initialState = {
  loading: false,
  account: null,
  smartContract: null,
  web3: null,
  balance:0,
  errorMsg: "",
  NftContract: null
};

const blockchainReducer = (state = initialState, action) => {
  switch (action.type) {
    case "CONNECTION_REQUEST":
      return {
        ...initialState,
        loading: true,
      };
    case "CONNECTION_SUCCESS":
      return {
        ...state,
        loading: false,
        account: action.payload.account,
        web3: action.payload.web3,
      };
    case "DISCONNECTION_REQUEST":
      return{
        ...initialState,
        loading: true,
      }
    case "DISCONNECTION_SUCCESS":
      return{
        ...state,
        loading: false,
        account: action.payload.account,
        web3: action.payload.web3,
      }
    case "DISCONNECTION_FAILED":
      return{
        ...initialState,
        loading:false,
        errorMsg: action.payload
      }
    case "CONNECTION_FAILED":
      return {
        ...initialState,
        loading: false,
        errorMsg: action.payload,
      };
    case "UPDATE_ACCOUNT":
      return {
        ...state,
        account: action.payload.account,
      };
    default:
      return state;
  }
};

export default blockchainReducer;
